.page {
  font-family: 'Suisse Intl', sans-serif;
  min-width: 320px;
  margin: 0 auto;
}

.hero {
    padding: 60px 80px 48px 80px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
    height: auto;
}

.hero__title {
    font-size: 48px;
    font-weight: 400;
    line-height: 55.58px;
    max-width: 620px;
    margin: 0;
}

.hero__title-orange {
    color: #FF9900;
}

.hero__cta-button_mobile {
    display: none;
}

.hero__cta-button {
    color: #fff;
    font-size: 24px;
    line-height: 32px;
    background-color: #FF9900;
    border: none;
    padding: 19px 60px;
    border-radius: 40px;
    margin-top: 79px;
    cursor: pointer;
    transition: all 0.3s ease;
}

.hero__cta-button:hover {
    -webkit-transform: translate3D(0, -1px, 0) scale(1.05);
            transform: translate3D(0, -1px, 0) scale(1.05);
}

.hero__image {
    max-width: 45%;
    height: auto;
    margin-top: 60px;
}

@media screen and (max-width: 1024px) {

    .hero {
        padding: 60px 40px 250px 40px;
    }

    .hero__image {
        max-width: 366px;
        height: auto;
        position: absolute;
        top: 170px;
        right: 40px;
    }

}

@media screen and (max-width: 768px) {

    .hero {
        flex-direction: column;
        height: auto;
        padding: 60px 16px 60px 16px;
    }

    .hero__title {
        font-size: 34px;
        line-height: 39.37px;
        width: 100%;
    }

    .hero__cta-wrapper {
        display: flex;
        flex-direction: column;
    }

    .hero__image {
        position: static;
        position: initial;
        width: 100%;
        margin-top: 30px;
    }

    .hero__cta-button_desktop {
        display: none;
    }

    .hero__cta-button_mobile {
        display: block;
        margin-top: 30px;
        padding: 19px 58px;
        width: 288px;
    }
}
.header {
    display: flex;
    justify-content: space-between;
    margin: 27px 80px 0 80px;
}

.header__mobile-wrapper {
    display: flex;
    align-items: center;
}

.logo-link {
    display: flex;
    align-items: center;
    max-width: 189px;
}

.logo-link:hover {
    cursor: pointer;
}

.logo {
    width: 100%;
    object-fit: contain;
}

.header__hamburger-button {
    display: none;
}

.header__x-icon-button {
    display: none;
}

.header__menu {
    display: flex;
    flex-direction: column;
}

.header__navbar {
    display: flex;
    padding: 0;
}

.header__list {
    margin-right: 32px;
    list-style: none;
    display: flex;
    align-items: center;
}

.header__list:last-child {
    margin-right: 0;
}

.header__link {
    text-decoration: none;
    font-weight: 400;
    font-size: 16px;
    line-height: 18.53px;
    color: #616161;
    transition: all 0.4s ease;
}

.header__link:hover {
    color: #000000;
    cursor: pointer;
}

.header__button {
    color: #FF9900;
    background-color: transparent;
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
    padding: 20px 32px;
    border: 2px solid #FF9900;
    border-radius: 30px;
    transition: all 0.4s ease;
}

.header__button:hover {
    color: white;
    cursor: pointer;
    background-color: #FF9900;
}

@media (max-width: 1024px) {
    .header {
        margin: 27px 40px 0 40px;
    }

    .logo-link {
        width: 137px;
        height: 25px;
    }

    .logo {
        width: 100%;
        height: 100%;
    }

    .header__list {
        margin-right: 20px;
    }
}

@media (max-width: 940px) {
    .header {
        margin: 27px 20px 0 20px;
    }

    .header__list {
        margin-right: 15px;
    }
}

@media (max-width: 840px) {

    .header__list {
        margin-right: 12px;
    }

    .header__link {
        font-size: 14px;
    }

    .header__button {
        padding: 15px 25px;
        font-size: 14px;
    }
}

@media (max-width: 740px) {
    .logo-link {
        width: 110px;
    }

    .header__list {
        margin-right: 10px;
    }

    .header__link {
        font-size: 12px;
    }

    .header__button {
        padding: 10px 20px;
        font-size: 12px
    }
}

@media (max-width: 640px) {
    .header {
        flex-direction: column;
    }

    .logo-link {
        width: 137px;
    }

    .header__mobile-wrapper {
        justify-content: space-between;
    }

    .header__hamburger-button {
        display: none;
        background-color: transparent;
        border: none;
    }

    .header__hamburger-button:hover {
        cursor: pointer;
    }

    .header__hamburger-button_active {
        display: block;
    }

    .header__x-icon-button {
        display: none;
        background-color: transparent;
        border: none;
    }

    .header__x-icon-button:hover {
        cursor: pointer;
    }


    .header__x-icon-button_active {
        display: block;
    }

    .header__menu {
        width: 100%;
        max-height: 0;
        min-height: 0;
        overflow: hidden;
    }

    .header__menu_type_active {
        transition: 1s;
        max-height: 540px;
        min-height: 540px;
    }

    .header__menu_type_inactive {
        transition: 0.7s;
        max-height: 0;
    }

    .header__navbar {
        flex-direction: column;
        align-items: center;
        margin-top: 66px;
        margin-bottom: 70px;
    }

    .header__list {
        margin-right: 0;
        margin-bottom: 30px;
    }

    .header__list:last-child {
        margin-bottom: 0;
    }

    .header__link {
        font-size: 34px;
        line-height: 39.37px;
        color: #000000;
        text-align: center;
    }

    .header__button {
        font-size: 24px;
        line-height: 32px;
        padding: 19px 0;
        color: #FFFFFF;
        background-color: #FF9900;
        width: 60%;
        margin: 0 auto;
        border-radius: 40px;
    }

}

@media (max-width: 320px) {
    .header{
        margin: 10px 16px 36px 16px;
    }

    .header__navbar {
        margin-top: 66px;
        margin-bottom: 70px;
    }

    .header__list {
        margin-bottom: 20px;
    }

    .header__button {

        width: 100%;
    }

}
.lead__title {
    font-size: 48px;
    line-height: 55.58px;
    color: #FFFFFF;
    margin: 0 auto;
}

.lead__subtitle {
    color: #DDDDDD;
    font-size: 30px;
    line-height: 40px;
    margin: 0 auto;
}

.lead__image-card-group {
    display: flex;
}


